.loginform {
  form {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 35%;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 3px 3px 10px 0px gray;
    max-width: 500px;
    min-width: 350px;
    margin: 130px auto 100px;
    label {
      display: block;
      width: 100%;
      margin: 10px 0px;
      input {
        display: block;
        width: 100%;
        padding: 4px;
        font-size: 18px;
        border-radius: 5px;
        border: 1px solid black;
        transition: 0.3s box-shadow ease-in-out, 0.3s border ease-in-out;
        &:focus,
        &:hover {
          box-shadow: 0px 0px 3px 0px var(--secondary-color);
          border-color: var(--secondary-color);
        }
        &:focus {
          outline: none;
        }
        & + p {
          visibility: hidden;
          display: none;
          margin: 10px 0px;
          padding: 8px;
          border-radius: 6px;
          width: fit-content;
          background-color: rgba($color: #ff0000, $alpha: 0.3);
          transition: 0.3s visibility ease-in;
        }
        &.visible {
          // background-color: rgba($color: #ff0000, $alpha: 0.3);
          box-shadow: 0px 0px 3px 0px red;
          & + p {
            display: block;
            visibility: visible;
          }
        }
      }
    }
    button {
      display: block;
      background-color: var(--secondary-color);
      color: white;
      width: 100%;
      padding: 10px;
      font-size: 20px;
    }
  }
}
